<template>
  <div class="root">
    <div class="detail-wrapper">
      <h1 class="">Bill Info</h1>
      <div class="detail">
        <span>AirPods 2</span>
        <span>$5,260</span>
      </div>
      <hr>
      <div class="total">
        <div class="total-detail">
        <span>Total :</span>
        <span>$5,260</span>
      </div>
      </div>
    </div>
    <div class="card-wrapper" id="cardWrapper" :data-background="backgroundImg">
      hi
    </div>
  </div>
</template>

<script>
import background from '@/assets/account-bank-background.png';
export default {
  name: 'CardDetail',
  data () {
    return {
      backgroundImg: background,
    }
  },
  created() {
  },
  mounted() {
    let backgroundAfterElement = document.getElementById('cardWrapper');    
    backgroundAfterElement.style = `background: url(${this.backgroundImg});background-color: rgba(255, 255, 255, 0.65);background-size: cover;`;
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.root {
  display: flex;
  height: 100vh;
  width: 100vw;
}
.detail-wrapper {
  background: #8693AB;
  width: 30%;
  height: 100%;
  padding: 50px;
}
.detail, .total-detail {
  margin: 40px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
}
.total {
  position: relative;
  height: 50%;
}
.total-detail {
  position: absolute;
  width: 100%;
  bottom: 40px;
}

hr {
  background-color: white;
  height: 4px;
  border: none;
}
.card-wrapper {
  flex-grow: 1;
  background-size: cover;
  background-color:rgba(255, 255, 255, 0.65);
}
.card-wrapper::after {
  content: "";
  /* background-image: url(attr("data-background")); */
  }
</style>


